import countryTemplate from './templates/templastes.hbs';
import api from './fetchCountries.js';
import conrtryList from './templates/country.hbs';
import {error} from '@pnotify/core';


const input = document.querySelector('input');
input.addEventListener('input', inputSearch);
function inputSearch(e) {
    const inputValue = e.target.value;
   api.fetchCountry(inputValue)
        .then(data =>
        {
            console.log(data);
            if (data.length===1) {
                 showdata(data)  
            }
            else if (data.length >= 2 && data.length <= 10) {
            markupCuntriesList(data)
     
            }
            else if (data.length > 10) {
                // defaultModules.set(PNotifyMobile, {});
errorCountry()          }
        }).catch(Baderror)
     
}
function showdata(data) {
    const countryCard = document.querySelector('.searchcountry').innerHTML = countryTemplate(data)

}

function markupCuntriesList(data) {
       const countryCardlist = document.querySelector('.searchcountry').innerHTML = conrtryList(data)
 
}
function errorCountry() {
    error({
  text: 'Пиши адекватно'
})
}
function Baderror() {
    error({
  text: 'Ти дурний?'
})
}